<template>
    <div id="Gangwar">
        <div class="container">
            <div class="container-center">
                <div class="left-bar">
                    <div class="left-inside"></div>
                </div>
                <div class="right-bar">
                    <div class="right-inside"></div>
                </div>
                <div class="points-left">
                    <p><span id="points-left-count">{{ scoreTeamOne }}</span></p>
                </div>
                <div class="points-right">
                    <p><span id="points-right-count">{{ scoreTeamTwo }}</span></p>
                </div>
                <img src="../../../assets/images/logos/MG13.png" class="logo-left" alt="TEAM_ONE_LOGO"/>
                <img src="../../../assets/images/logos/MG13.png" class="logo-right" alt="TEAM_ONE_LOGO"/>

                <p class="timer">
                    <span v-if="seconds >= 10 && minutes >= 10" style="display: block;">{{ minutes }}:{{ seconds }}</span>
                    <span v-else-if="seconds < 10 && minutes >= 10" style="display: block;">{{ minutes }}:0{{ seconds }}</span>
                    <span v-else-if="seconds >= 10 && minutes < 10" style="display: block;">0{{ minutes }}:{{ seconds }}</span>
                    <span v-else-if="seconds < 10 && minutes < 10" style="display: block;">0{{ minutes }}:0{{ seconds }}</span>
                </p>
            </div>

        </div>
    </div>
</template>

<script>
import Windows from "../../js/windows"
// eslint-disable-next-line no-unused-vars
let timer

export default Windows.register({
    name: "Gangwar",
    props: {
        data: String
    },
    data() {
        return {
            gangwarData: JSON.parse(this.data),
            teamOne: "",
            teamTwo: "",
            scoreTeamOne: 0,
            scoreTeamTwo: 0,
            minutes: 0,
            seconds: 0
        }
    },
    methods: {
        timer() {
            this.seconds--

            if (this.seconds < 0) {
                this.minutes--

                if (this.minutes < 0) {
                    // TODO: end match
                } else {
                    this.seconds = 59
                }
            }
        }
    },
    created() {
        this.teamOne = require('../../../assets/images/logos/' + this.gangwarData.teamOne.name + '.png')
        this.teamOne = require('../../../assets/images/logos/' + this.gangwarData.teamTwo.name + '.png')

        this.minutes = this.gangwarData.data.maxPlayTime
        this.scoreTeamOne = this.gangwarData.data.maxScore
        this.scoreTeamTwo = this.gangwarData.data.maxScore
    },
    mounted() {
        timer = setInterval(this.timer, 1000)
    },
    destroyed() {
        clearInterval(this.timer)
        timer = null
    },
    watch: {
        scoreTeamOne: function(val) {
            console.log(document.querySelector('.left-inside'))
            document.querySelector('.left-inside').style.width = + val + "%"
        },
        scoreTeamTwo: function(val) {
            document.querySelector('.right-inside').style.width = + val + "%"
        }
    }
})
</script>

<style lang="scss" scoped>

.left-bar {
    position: absolute;
    left: 28.66%;
    top: 5.78%;
    background-color: #1f1f1fce;
    width: 13.3%;
    height: 0.7vh;
    border-radius: 0.15vh;
}

.right-bar {
    position: absolute;
    left: 58.03%;
    top: 5.78%;
    background-color: #1f1f1fce;
    width: 13.3%;
    height: 0.7vh;
    border-radius: 0.15vh;
}

img.logo-left {
    width: 2%;
    height: auto;
    position: absolute;
    left: 45.06%;
    top: 4.32%;
}

img.logo-right {
    width: 2%;
    height: auto;
    position: absolute;
    left: 53.15%;
    top: 4.32%;
}

.points-left {
    position: absolute;
    left: 42.05%;
    top: 5%;
    width: 3.15%;
    height: 2vh;
    background-color: transparent;
    padding-bottom: 1vh;
}

.points-left p {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.5vh;
    text-align: center;
    color: white;
    letter-spacing: 0.1vh;
    text-shadow: 1px 1px #000;
}

.points-right {
    position: absolute;
    left: 54.995%;
    top: 5%;
    width: 2.95%;
    height: 2vh;
    background-color: transparent;
    padding-bottom: 1vh;
}

.right-bar .right-inside {
    width: 100%;
    height: 100%;
    background-color: #B93729;
    float: right;
}

.left-bar .left-inside {
    width: 100%;
    height: 100%;
    background-color: #439ECC;
}

.points-right p {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.5vh;
    text-align: center;
    color: white;
    letter-spacing: 0.1vh;
    text-shadow: 1px 1px #000;
}

.container-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
}

.container-center p.timer {
    position: absolute;
    left: 50%;
    top: 6%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 3vh;
    letter-spacing: 0.1vh;
    text-shadow: 1px 1px #000;
}
</style>