<template>
    <div id="OutfitSelector">
        <div class="container">
            <div @click="left()">
                <a>
                    <img class="left" src="../../../assets/images/outfit/left.png" alt="">
                </a>
            </div>
            <div @click="right()">
                <a>
                    <img class="right" src="../../../assets/images/outfit/right.png" alt="">
                </a>
            </div>
            <div @click="chooseClothing()">
                <button>Auswhlen</button>
            </div>
        </div>
    </div>
</template>

<script>
import Windows from "../../js/windows"

export default Windows.register({
    name: "OutfitSelector",
    props: {
        data: String
    },
    data() {
        return {
            outfitData: JSON.parse(this.data),
            currentIndex: 1,
            maxIndex: 0
        }
    },
    methods: {
        chooseClothing() {
            this.triggerServer("Server:TeamClothing:SelectTeamClothing", this.oufitData)
            this.trigger("client:deleteTeamClothingCamera")
            this.showCursor(false)
            this.dismiss()
        },
        left() {
            if (this.currentIndex <= 1) {
                this.currentIndex = this.maxIndex
                return
            }

            this.currentIndex--
        },
        right() {
            if (this.currentIndex > this.maxIndex) {
                this.currentIndex = 1
                return
            }

            this.currentIndex++
        }
    },
    mounted() {
        this.maxIndex = this.outfitData.length
        this.trigger("client:createTeamClothingCamera")

        this.showCursor(true)
    },
    watch: {
        currentIndex: function(val) {
            this.trigger("client:teamClothing:SetTeamClothing", this.outfitData)
        }
    }
})
</script>

<style lang="scss" scoped>
.container {
    position: absolute;
    width: 100%;
    height: 100%;

    img {
        position: absolute;
        width: 12.5vh;
        height: auto;
        top: 50%;
        transform: translate(0%, -50%);
    }

    .left {
        position: absolute;
        left: 0;
        margin-left: 2.5vh;
    }

    .right {
        position: absolute;
        right: 0;
        margin-right: 2.5vh;
    }

    button {
        position: absolute;
        left: 50%;
        top: 96%;
        transform: translate(-50%, -50%);
        width: 10vw;
        height: 4vh;
        border: 0.2vh solid #fff;
        outline: none;
        background-color: #1f1f1fa1;
        color: white;
        font-size: 2vh;
        transition: all 250ms;

        &:hover {
            color: black;
            background-color: #fff;
        }
    }
}
</style>