<template>
    <div v-if="visible">
        <component v-for="window in windows" :key="window.name" :is="window.name" :data="window.data"></component>

        <div class="top-center">
        </div>

        <div class="center">
        </div>

        <div class="center-right">
        </div>

        <div class="top right">
        </div>

        <div class="top left">
        </div>

        <div class="bottom-center">
        </div>

        <div class="bottom-center2">
        </div>

        <div class="bottom right">
            <Speedometer />
        </div>

        <div class="bottom left">
            <PlayerPanel />
        </div>
    </div>
</template>

<script>
import Components from "../js/components"

import PlayerPanel from "./PlayerPanel/PlayerPanel"
import Speedometer from "./Speedometer/Speedometer"

export default Components.register({
    name: "Hud",
    components: {
        PlayerPanel,
        Speedometer,
    },
    data() {
        return {
            visible: false,
            windows: []
        }
    },
    methods: {
        addWindow: function(window, data = null) {
            this.windows.push({ name: window, data: data })
        },
        removeWindow: function(window, dataFunction = null) {
            for (var i = this.windows.length - 1; i >= 0; i--) {
                if (this.windows[i].name === window) {
                    if (dataFunction != null) {
                        if (!dataFunction(window.data)) continue
                    }
                    this.windows.splice(i, 1)
                    return
                }
            }
        },
        setState(val) {
            this.visible = val
        }
    }
})
</script>

<style lang="scss" scoped>
.center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}

.center-right {
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translate(0, -50%);
    right: 1vw;
}

.bottom {
    z-index: 1;
    position: absolute;
    display: flex;
    align-items: flex-end;
    &.right {
        bottom: 1vh;
        right: 1vw; //1vw;
    }
    .smartphone {
        margin-left: 1vw;
    }
}

.top.left{
    position: static;
}

.bottom.left{
    bottom: 25%;
    left: 1%;
}

.top {
    z-index: 1;
    position: absolute;
    top: 7vh;
    &.right {
        right: 1vw;
    }
}

.top-center {
    position: absolute;
    z-index: 1;
    top: 7vw;
    left: 50%;
    transform: translateX(-50%);
}

.bottom-center {
    position: absolute;
    z-index: 1;
    bottom: 7vw;
    left: 50%;
    transform: translateX(-50%);
}

.bottom-center2 {
    position: absolute;
    z-index: 1;
    bottom: 1vw;
    left: 50%;
    transform: translateX(-50%);
}
</style>
