<template>
    <div id="Speedometer">
        <div class="container" v-if="visible === true">
            <p class="speed">{{ speed }}<span class="kmh">KMH</span></p>
            <div class="rpm-container">
                <div class="rpm"></div>
            </div>
            <p class="gear">GEAR <span id="gear">{{ gear }}</span></p>
        </div>
    </div>
</template>

<script>
import Components from "../../js/components"

export default Components.register({
    name: "Speedometer",
    props: {
        data: String
    },
    data() {
        return {
            speed: 0,
            gear: 0,
            rpm: 0,
            visible: false
        }
    },
    methods: {
        setShown(val) {
            this.visible = val
        }
    },
    watch: {
        rpm: function (val) {
            document.querySelector('.rpm').style.width = val + '%'
        }
    }
})
</script>

<style lang="scss" scoped>
.container {
    padding-right: 5vh;

    p.speed {
        font-size: 4vh;
        color: white;
        text-shadow: 0.2vh 0.2vh 0.4vh #000000;
    }

    span.kmh {
        font-size: 2vh;
        margin-left: 0.5vh;
        color: white;
    }

    .rpm-container {
        width: 160%;
        height: 1.25vh;
        border-radius: 0.25vh;
        background-color: white;
    }

    .rpm-container .rpm {
        width: 100%;
        height: 100%;
        background-color: #fc5e03;
        float: left;
    }

    p.gear {
        color: white;
        font-size: 2.5vh;
        margin-top: 0.2vh;
        color: #fc5e03;
        text-shadow: 0.2vh 0.2vh 0.4vh #000000;
    }
}
</style>