<template>
    <component :is="window" :data="data"></component>
</template>

<script>
import Components from '../js/components'

import MainMenu from "./MainMenu/MainMenu"
import Garage from "./Garage/Garage"
import Gangwar from "./Gangwar/Gangwar"
import Inventory from "./Inventory/Inventory"
import OutfitSelector from "./OutfitSelector/OutfitSelector"

export default Components.register({
    name: 'Windows',

    data: function () {
        return {
            window: '',
            data: {},
        }
    },

    methods: {
        show: function (window, data = null) {
            this.data = data
            this.window = window
        },
    },

    components: {
        MainMenu,
        Garage,
        Gangwar,
        Inventory,
        OutfitSelector,
    },
})
</script>