<script>
import Windows from "../../js/windows"

let value = 0
let regex = /[^A-Za-z0-9_-]/

let languageMessages = require("../../../assets/data/languageMessages.json")

export default Windows.register({
    name: 'MainMenu',
    props: {
        data: String
    },
    data() {
        return {
            mainMenuData: JSON.parse(this.data),
            index: 0,
            tabIndex: 0,
            username: "",
            image: "",
            translatedMessage: {},
            cssProps: {
                backgroundImage: `url(${require('@/assets/images/mainmenu/logo.png')})`
            }
        }
    },
    methods: {
        addMouseEvent() {
            setTimeout(() => {
                document.querySelector(".available-ffa-arenas").addEventListener("mousewheel", this.mouseEvent, {passive: false})
            }, 100)
        },
        removeMouseEvent() {
            document.querySelector(".available-ffa-arenas").removeEventListener("mousewheel", this.mouseEvent, {passive: true})
        },
        mouseEvent(event) {
            event.preventDefault()

            if (event.wheelDelta / 120 > 0) {
                document.querySelector(".available-ffa-arenas").scrollLeft = value += 100
            } else {
                document.querySelector(".available-ffa-arenas").scrollLeft = value -= 100
                if (value < 0) {
                    value = 0
                }
            }
        },
        badName(val) {
            if (val.includes("IlIl") || val.includes("lIlI") || val.includes("IIII") || val.includes("llll")) {
                return true
            }

            return false
        },
        login() {
            if (regex.test(this.username) || this.username.length < 4 || this.username.length > 20 || this.badName(this.username)) {
                return
            }

            this.triggerServer("Server:Login:RequestLogin", this.username)
            this.index = 1
        },
        chooseTeam(id) {
            if (id > 0 && id < 10) {
                this.triggerServer("Server:MainMenu:RequestCurrentTeam", id)
                this.dismiss()
            }
        },
        chooseArena(id) {
            console.log(id)
            if (id > 0 && id < 10) {
                this.triggerServer("Server:MainMenu:SelectFfaArena", id)
                this.dismiss()
            }
        },
        updateLanguage(lang) {
            switch (lang) {
                case 1:
                    this.translatedMessage = languageMessages.english
                    break
                case 2:
                    this.translatedMessage = languageMessages.russian
                    break
                case 3:
                    this.translatedMessage = languageMessages.german
                    break
                default:
                    this.translatedMessage = languageMessages.english
                    break
            }
        }
    },
    mounted() {
        if (this.mainMenuData.playerData == null) {
            this.index = 0
            this.cssProps.backgroundImage = `url(${require('@/assets/images/mainmenu/backgrounds/' + Math.floor(Math.random() * 13) + '.png')})`
        } else if (this.mainMenuData.playerData.HasAccount === true) {
            this.index = 1
        }

        this.cssProps.backgroundImage = `url(${require('@/assets/images/mainmenu/backgrounds/' + Math.floor(Math.random() * 13) + '.png')})`

        this.showCursor(true)
    },
    computed: {
        openFactionList() {
            return this.mainMenuData.teamData.filter(x => x.IsPrivate == false)
        },
        privateFactionList() {
            return this.mainMenuData.teamData.filter(x => x.IsPrivate == true)
        }
    },
    created() {
        if (this.mainMenuData == null) {
            this.index = 0
            this.translatedMessage = languageMessages.english
            return
        }

        switch (this.mainMenuData.playerData.Language) {
            case 1:
                this.translatedMessage = languageMessages.english
                break
            case 2:
                this.translatedMessage = languageMessages.russian
                break
            case 3:
                this.translatedMessage = languageMessages.german
                break
            default:
                this.translatedMessage = languageMessages.english
                break
        }
    }
})
</script>

