import mp from "../../mp"
import global from "./global"

if (global.components == undefined) {
    global.components = {}
}

class Components {
    static register(component) {
        component.beforeMount = function() {
            global.components[component.name] = this
            mp.trigger("componentReady", component.name)
        }
        component.beforeDestroy = function() {
            delete global.components[component.name]
        }
        if (component.methods == undefined) {
            component.methods = {}
        }
        component.methods.trigger = function(eventName, ...eventArgs) {
            mp.trigger(
                eventName,
                ...eventArgs
            )
        }
        component.methods.triggerServer = function(eventName, ...eventArgs) {
            mp.trigger(
                "componentTriggerServerEvent",
                eventName,
                ...eventArgs
            )
        }
        component.methods.showCursor = function(state) {
            mp.trigger("showCursor", state)
        }
        component.methods.boob = function() {
            mp.trigger("componentDestroy", component.name)
            delete global.components[component.name]
            this.dismiss();
        }
        return component
    }
}

export default Components
