<template>
    <div id="Garage">
        <div class="container">
            <div class="title">
                <p>Elite Deathmatch</p>
            </div>
            <div class="content" v-for="vehicleData in GarageVehicles" :key="vehicleData.Id">
                <div class="content-item">
                    <p class="title">{{ vehicleData.Name }}</p>
                    <button @click="parkOutVehicle(vehicleData.Name)">{{ translatedMessage.garage.button }}</button>
                </div>
                <div class="content-item">
                    <div class="vehicleLevel">
                        <p>{{ vehicleData.Level }}</p>
                    </div>
                </div>
            </div>
            <div class="nav-control">
                <div>
                    <i class="fas fa-arrow-left" @click="decrease()"></i>
                </div>
                <div>
                </div>
                <div>
                    <i class="fas fa-arrow-right" @click="increase()"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Windows from "../../js/windows"
import languageMessages from "../../../assets/data/languageMessages.json"

export default Windows.register({
    name: "Garage",
    props: {
        data: String
    },
    data() {
        return {
            garageData: JSON.parse(this.data),
            currentIndex: 1,
            translatedMessage: {},
            maxIndex: 0
        }
    },
    methods: {
        decrease() {
            if (this.currentIndex <= 1) {
                this.currentIndex = this.maxIndex
                return
            }

            this.currentIndex--
        },
        increase() {
            if (this.currentIndex >= this.maxIndex) {
                this.currentIndex = 1
                return
            }

            this.currentIndex++
        },
        parkOutVehicle(Hash) {
            this.triggerServer("Server:Garage:ParkOut", Hash)
            this.dismiss()
        }
    },
    mounted() {
        this.showCursor(true)
    },
    destroyed() {
        this.showCursor(false)
    },
    computed: {
        GarageVehicles() {
            return this.garageData.vehicles.filter(x => x.Id === this.currentIndex)
        }
    },
    created() {
        switch (this.garageData.playerData.language) {
            case 1:
                this.translatedMessage = languageMessages.english
                break
            case 2:
                this.translatedMessage = languageMessages.russian
                break
            case 3:
                this.translatedMessage = languageMessages.german
                break
            default:
                this.translatedMessage = languageMessages.english
                break
        }

        this.maxIndex = this.garageData.vehicles.length
    }
})
</script>

<style lang="scss" scoped>
#Garage {
    position: relative;
    //background-image: url("../../../assets/images/mainmenu/backgrounds/0.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 100%;
    height: 100vh;
}

.container {
    position: absolute;
    left: 5.2%;
    top: 6.1%;
    width: 90%;
    height: 90%;

    .title {
        p {
            color: white;
            font-size: 8vh;
            letter-spacing: 0.1vh;
            text-shadow: 0 0 0.4vh #fc5e03;
        }
    }

    .content {
        display: flex;

        .content-item {
            flex: 1;

            button {
                margin-top: 5%;
                outline: none;
                border: none;
                color: white;
                text-shadow: 0.2vh 0.2vh 0.4vh #000000;
                width: 50%;
                height: 5vh;
                font-size: 3vh;
                background-color: rgba(0, 0, 0, 0.85);
                transition: 0.2s ease-in;

                &:hover {
                    background-color: rgba(255, 255, 255, 0.85);
                    color: black;
                    text-shadow: 0.2vh 0.2vh 0.4vh #000000;
                }
            }
        }

        .content-item-bottom {
            bottom: 0;
        }

        .title {
            margin-top: 5%;
            color: white;
            font-size: 7vh;
            letter-spacing: 0.1vh;
            text-shadow: 0.2vh 0.2vh 0.4vh #000000;
        }

        .vehicleLevel {
            float: right;
            display: flex;
            border: 0.4vh solid white;
            width: 6vh;
            height: 6vh;
            border-radius: 50%;
            justify-content: center;
            align-items: center;
            box-shadow: 0 0 0.4vh #000000;
            margin-right: 0.5vh;
            margin-top: 0.5vh;
            font-size: 2.5vh;

            p {
                color: white;
                text-shadow: 0.2vh 0.2vh 0.4vh #000000;
                padding-right: 0.4vh;
            }
        }
    }

    .nav-control {
        position: absolute;
        display: flex;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        i {
            display: flex;
            margin: 5vh;
            color: white;
            border: 0.2vh solid white;
            box-shadow: 0 0 0.4vh #000000;
            border-radius: 50%;
            justify-content: center;
            align-items: center;
            width: 3vh;
            height: 3vh;
            text-shadow: 0.2vh 0.2vh 0.4vh #000000;
        }

        i:hover {
            transform: scale(1.1);
        }
    }
}
</style>