                    </svg>
                    <span v-if="isNaN(parseFloat((kills/deaths)).toFixed(1))" class="text">K/D: 0</span>
                    <span v-else-if="!isNaN(parseFloat((kills/deaths)).toFixed(1))" class="text">K/D: {{ parseFloat((kills/deaths)).toFixed(1) }}</span>
                </li>
