                </li>
            </ul>

            <!-- <audio src="./sounds/hitsound.mp3" id="hitmarkerSound"></div> -->
        </div>
    </div>
</template>

<script>
import Components from "../../js/components"

export default Components.register({
    name: 'PlayerPanel',
    props: {
        data: String
    },
    data() {
        return {
            kills: 0,
            deaths: 0,
            level: 0,
            kd: 0
        }
    },
    methods: {
        initializeHud(kills, deaths, level) {
            this.kills = kills
            this.deaths = deaths
            this.level = level
        }
    },
    mounted() {
        this.kd = parseFloat((this.kills/this.deaths)).toFixed(1)
    }
})
</script>

