                        <p id="daily-missions-title">{{ translatedMessage.inventory.currentKitName }}</p>
                        <div class="stripe-left"></div>
                        <div class="stripe-right"></div>
                    </div>
                    <div class="currentweapons">
                        <ul>
                            <li style="width: 47.7%; height: 13.75vh;">
                                <img  :src="require('../../../assets/images/weapons/' + selectedWeaponOne + '.png')"
                                     style="width: 70%; height: auto;"
                                     id="firstweapon">
                            </li>
                            <li style="width: 47.7%; height: 13.75vh;">
                                <img :src="require('../../../assets/images/weapons/' + selectedWeaponTwo + '.png')"
                                     style="width: 70%; height: auto;"
                                     id="seccondweapon">
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Windows from "../../js/windows"
import languageMessages from "../../../assets/data/languageMessages.json"

export default Windows.register({
    name: "Inventory",
    props: {
        data: String
    },
    data() {
        return {
            inventoryData: JSON.parse(this.data),
            index: 0,
            itemClicked: false,
            currentItem: {},
            translatedMessage: {},
            selectedWeaponOne: "",
            selectedWeaponTwo: ""
        }
    },
    methods: {
        useItem() {
            this.itemClicked = false

            this.currentItem.Amount--
            this.currentItem = {}

            console.log(JSON.stringify(this.inventoryData))
        },
        removeItem() {
            this.itemClicked = false

            this.currentItem.Amount--
            this.currentItem = {}
        },
        abortItemMenu() {
            this.itemClicked = false
            this.currentItem = {}
        },
        setCurrentItem(itemData) {
            this.itemClicked = true
            this.currentItem = itemData
        },
        selectHandWeapon(hash) {
            this.selectedWeaponOne = hash
        },
        selectLongWeapon(hash) {
            this.selectedWeaponTwo = hash
        }
    },
    created() {
        switch (this.inventoryData.data.language) {
            case 1:
                this.translatedMessage = languageMessages.english
                break
            case 2:
                this.translatedMessage = languageMessages.russian
                break
            case 3:
                this.translatedMessage = languageMessages.german
                break
            default:
                this.translatedMessage = languageMessages.english
                break
        }

        this.selectedWeaponOne = this.inventoryData.data.firstOne
        this.selectedWeaponTwo = this.inventoryData.data.firstTwo
    },
    computed: {
        InventoryItems() {
            return this.inventoryData.inventory.filter(x => x.Amount > 0)
        }
    },
    destroyed() {
        this.triggerServer("InventoryUpdate", this.inventoryData)
    }
})
</script>

